<template>
    <div class="feedback-layout">
        <header class="feedback-header">
            <a @click="goPrePage()" class="feedback-cancle">取消</a>
            <h2 class="feedback-title">{{fromName}}</h2>
            <button  class="feedback-send" id="feedback-send" :disabled="!isAble" @click="sendMoreMsg()">发送</button>
        </header>
        <div class="feedback-content-box">
            <textarea class="feedback-content" placeholder="写回复......" name="content" id="feedback-area" cols="10" rows="20" v-model="feedContent">
            </textarea>
        </div>
    </div>
</template>
<script>
  import Api from '../../api/api';
  import autoSize from 'autosize';
  import scriptFilter from '../../utils/filterScript'
    export default{
        data(){
            return {
                isAble:false,
                feedContent:'',
                commentId:-1,
                fromId:-1,
                toId:-1,
                fromName:'回复',

            }
        },
        mounted(){
            autoSize(document.querySelectorAll('textarea'));
            this.commentId = this.$route.params.commentId;
            this.fromId = this.$route.params.fromId;
            this.toId = this.$route.params.toId;
            this.fromName = `回复${this.$route.params.fromName}`;
            /**
             * 获得焦点,改变样式
             */
            const contentBox = $('#feedback-area');

            const feedBtn = $('#feedback-send');
            const _this = this;
            /**
             * 获得焦点,改变样式
             */
            contentBox.focus();
            _this.changeBtnBg(_this,contentBox,feedBtn);
        },
        methods:{
            sendMoreMsg(){
                if(scriptFilter(this.feedContent)){
                    this.showMsg("内容不能包含奇怪字符!");
                    return;
                }
                if(this.feedContent!==null && this.feedContent!==''){
                    this.commitReply(this.commentId,this.fromId,this.toId);
                }else{
                    this.showMsg('请填写回复内容哦');
                }
            },
          /**
           * 提交回复
           * */
          commitReply(commentId,fromId,toId){
              const _this = this;
              let to_uid={
                id:toId,
              };
              if(commentId!==-1 && $.isNumeric(commentId) || fromId!==-1 && $.isNumeric(fromId)){
                this.$http.post(Api.REPLYTOREPLY,{
                  content:_this.feedContent,
                  reply_id:1,
                  comment_id:commentId,
//                  from_uid:fromId,
                  to_uid:to_uid
                }).then((res)=>{
                  if(res.status ===200){
                    history.back(-1);//返回上一页
                  }else{
                    _this.showMsg("回复失败，请重试!");
                    _this.$store.dispatch('hideLoading');
                  }
                },(err)=>{
                  _this.showMsg("回复失败，请重试!");
                  _this.$store.dispatch('hideLoading');
                });
              }
          },
          showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
            /**
             * 返回上一级元素
             */
            goPrePage(){
                window.history.go(-1);
            },
            setBackGround(ele,bg,color,border) {
                ele.css({
                    'background':bg,
                    'color':color,
                    'border':border
                })
            },
            /**
             * 根据输入内容设置发送按钮的样式
             * @param _this
             * @param contentBox:内容dom元素
             * @param feedBtn:按钮元素
             */
            changeBtnBg(_this,contentBox,feedBtn){
                $(document).on('input propertychange', 'textarea', function() {
                    //function code
                    _this.feedContent = contentBox.val();
                    if(_this.feedContent!=='null' && _this.feedContent!==''&& _this.feedContent.length>0){
                        _this.isAble = true;
                        _this.setBackGround(feedBtn,'#20a0ff','#ffffff','1px solid #20a0ff');
                    }else{
                        _this.isAble = false;
                        _this.setBackGround(feedBtn,'#ffffff','#9c9c9c','1px solid #9c9c9c');
                    }
                });
            }
        },
      watch:{
        $route(to){
          //对传递的id进行验证(防止篡改)
          let reg=/replytoreply\/\d+/;
          if(reg.test(to.path)){
          /*  let commentId=this.$route.params.commentId || 0;
            let fromId=this.$route.params.fromId || 0;
            let toId=this.$route.params.toId || 0;*/
            this.commentId = this.$route.params.commentId;
            this.fromId = this.$route.params.fromId;
            this.toId = this.$route.params.toId;
            this.fromName = `回复${this.$route.params.fromName}`;
          }
        }
      },
    }
</script>
<style scoped>
    @import "../../assets/css/feedback.css";
</style>



